<template lang="pug">
.curso-main-container.pb-3
  BannerInterno
  .container.tarjeta.tarjeta--blanca.p-4.p-md-5.mb-5
    .titulo-principal.color-acento-contenido
      .titulo-principal__numero
        span 2
      h1  Aprovechamiento y tratamiento de los residuos slidos no peligrosos
    p.mb-5(data-aos='fade-right') El proceso de aprovechamiento de residuos slidos no peligrosos se genera, principalmente, con papel, cartn, vidrio, metal, plstico.  
    .row.justify-content-center.mb-5
      .col-lg-6.mb-lg-0.mb-3(data-aos="fade-right")
        .cajon.color-acento-contenido.p-4.mb-4(style="background-color: #eef6d7 ").h-100
          p.mb-0  #[b Tal proceso de aprovechamiento se da cuando los materiales sobrantes o descartados de cualquier proceso productivo son restituidos o acondicionados para nuevos servicios] que, adems, son favorables al ambiente. Por lo general, el aprovechamiento de los residuos se da mediante la reutilizacin y el reciclaje.
      .col-lg-6(data-aos="fade-left"): img(src='@/assets/curso/temas/2/1.png', alt='')
    Separador
    #t_2_1.titulo-segundo.color-acento-contenido(data-aos='fade-right')
      h2 2.1 Papel y cartn
    .card-tema.p-4.mb-5(style="background-color: #fff9e5")(data-aos="zoom-in")
      img.card-1.d-xl-block.d-none(src='@/assets/curso/temas/2/2.png', alt='')
      .row.justify-content-center.align-items-center
        .col-lg-4.col-6
          img.d-xl-none.d-block(src='@/assets/curso/temas/2/2.png', alt='')
        .col-lg-8
          p.mb-0.p-2 El papel y el cartn son productos que se requieren para comunicacin de informacin, trasmisin de conocimiento y cultura, empacar, transportar, y un sinnmero de actividades de la vida diaria. En Colombia, el papel es producido a partir de fibras de celulosa obtenidas del bagazo de la caa de azcar - residuo resultado de un proceso agroindustrial - o de madera proveniente de plantaciones forestales certificadas de pino y eucalipto, que son cosechadas y renovadas con fines comerciales, tal como se hace con otros cultivos agrcolas. Estas dos fuentes de celulosa tienen la gran ventaja de provenir de un recurso renovable, que tambin es reciclable y biodegradable.
    p.mb-5(data-aos='fade-right') #[b En relacin con el reciclaje de papel y cartn, tenga en cuanta algunos aspectos clave, como los que se mencionan enseguida:]
    .row.justify-content-center.mb-5
      .col-lg-8(data-aos="zoom-in")
        AcordionA.mb-5.acordion-icon(tipo="a" clase-tarjeta="tarjeta tarjeta--gris")
          .row(titulo="Vida til y nueva vida").justify-content-center
            .col-md-7.mb-4.mb-md-0
              p El reciclaje prolonga la vida til de las fibras de celulosa al permitir que productos que ya han cumplido su vida til se puedan utilizar como materia prima en la elaboracin de nuevos papeles o cartones, convirtindose en otra fuente importante de abastecimiento de fibra de la industria papelera colombiana. 
            .col-md-5.col-6
              figure
                img.m-auto(src='@/assets/curso/temas/2/3.png', alt='Texto que describa la imagen')
          .row(titulo="Colombia responsable").justify-content-center
            .col-md-7.mb-4.mb-md-0
              p El reciclaje de papel en Colombia inici en los aos 40, cuando las empresas productoras de cajas de cartn adecuaron su proceso productivo para incorporar desperdicios de cartn como insumo. La tendencia de recuperacin de papel desde ese entonces ha sido creciente, alcanzando en los ltimos 5 aos un ndice de recoleccin promedio del 47% del papel consumido.
            .col-md-5.col-6
              figure
                img.m-auto(src='@/assets/curso/temas/2/4.png', alt='Texto que describa la imagen')
          .row(titulo="Reduccin de necesidades y de consumo").justify-content-center
            .col-md-7.mb-4.mb-md-0
              p Con l, se reducen las necesidades de fibra virgen y, por lo tanto, las de los insumos y materias primas utilizados para producirla. Sin embargo, el reciclaje de las fibras de celulosa no es infinito, ya que en los procesos de repulpeo y refinacin las fibras van perdiendo propiedades como longitud y resistencia, por lo que es necesario mezclarlas con fibra virgen para la produccin de algunos productos.
            .col-md-5.col-6
              figure.mb-3
                img.m-auto(src='@/assets/curso/temas/2/5.png', alt='Texto que describa la imagen')
          div(titulo="Almacenamiento").justify-content-center
            p.mb-0 El papel debe almacenarse en instalaciones cerradas con el fin de que no se altere su calidad ni adquiera humedad. Cuando el material es recibido en la bodega, se debe medir la humedad con un higrmetro, esta no debe pasar del 10%, ya que cualquier papel o cartn con un porcentaje adicional ser descartado.
            a.boton.color-acento-botones.indicador__container(@click="modal1 = true").mb-3
              span Ciclo del papel y cartn
              .indicador--click(v-if="mostrarIndicador")
          .row(titulo="Perjudicial").justify-content-center
            .col-md-7.mb-4.mb-md-0
              p Se consideran elementos perjudiciales para el proceso de reciclaje aquellos no fibrosos que deterioran la calidad del papel y el cartn, y que, adems, perjudican el proceso productivo, los equipos y el producto final: trapos, plsticos, cauchos, hule, poliestireno expandido, vidrios, colillas de cigarrillo, residuos de aceites, deshechos de comidas, pinturas no solubles, humedad excesiva, cuerdas de todo tipo, alambre, zunchos, metales de todo tipo, cabuyas, piedras, arena, madera, tierra o barro, residuos de cemento, papel alquitrn, de seguridad, qumico, moneda, carbn, encerados, celofn, glassine, de fotografa, qumico, papeles impregnados de parafina, de negro humo, de grasa, aceite o manteca.
            .col-md-5.col-6
              figure
                img.m-auto(src='@/assets/curso/temas/2/6.png', alt='Texto que describa la imagen')
          .row(titulo="De sumo cuidado").justify-content-center
            .col-md-7.mb-4.mb-md-0
              p Es importantsimo que, en el proceso de reciclaje, los papeles no estn revestidos de plstico o con satinados plsticos, de cera, aluminio, barniz o laca, papeles con resistencia en hmedo o con barnizado resistentes a la humedad, papeles adhesivos con alma de tela o plstico, papeles o cintas con adhesivos no solubles en agua, moos o lomos con cualquier pegante, papeles laminados con aluminio u otros materiales, papeles deteriorados por accin nociva del tiempo, papeles artificiales elaborados a partir de plsticos o polmeros, canastas, bandejas o separadores de empaques para huevos o frutas.
            .col-md-5.col-6
              figure.mb-3
                img.m-auto(src='@/assets/curso/temas/2/7.png', alt='Texto que describa la imagen')
        .tarjeta.p-5(style="background-color: #dee9f0 ")(data-aos="zoom-in")
          .row.justify-content-center.align-items-center.mb-5
            .col-1: img(src='@/assets/curso/temas/atencion.svg', alt='')
            .col-lg-11
              p.mb-0.fw-bold Para profundizar en este punto del contenido, se le invita a consultar cuidadosamente los siguientes recursos. 
          a.anexo.mb-4(:href="obtenerLink('downloads/Anexo_1_Tabla1_CategoriaDeReciclajePapelCarton.pdf')" target="_blank")
            .row.justify-content-around.align-items-center
