    #t_2_2.titulo-segundo.color-acento-contenido(data-aos='fade-right')
      h2 2.2 Plsticos 
    .cajon-b.color-acento-contenido.p-3.mb-5(data-aos="zoom-in")
      p.mb-0 El plstico es un material slido sinttico, llamado polmero, que puede ser moldeado mediante calor y/o presin y cuyo componente principal es el carbono. Hay una gran variedad de clases de plstico, dependiendo de su composicin, y este factor es fundamental cuando se lleva a cabo el reciclaje de plstico. Los plsticos se clasifican en tres grupos, segn la disposicin de las macromolculas que los constituyen.
    .row.justify-content-center.mb-5
      .col-lg-6.mb-lg-0.mb-3(data-aos="fade-right")
        .tarjeta-avatar
            img(src='@/assets/curso/temas/2/9.png' alt='AvatarTop' style='width: 300px')
            .tarjeta.w-100(style="background-color: #ecf7f4")
              .p-4
                h5.text-center Termoplsticos
                p Es un plstico que, a temperatura ambiente, es plstico o deformable, se convierte en un lquido cuando se calienta y se endurece en un estado vtreo cuando se enfra suficiente.
      .col-lg-6.mb-lg-0.mb-3(data-aos="fade-left")
        .tarjeta-avatar
            img(src='@/assets/curso/temas/2/10.png' alt='AvatarTop' style='width: 300px')
            .tarjeta.w-100(style="background-color: #fdf8e4")
              .p-4
                h5.text-center Termoestables
                p Son materiales que, una vez que han sufrido el proceso de calentamiento-fusin y formacin-solidificacin, se convierten en materiales rgidos que no vuelven a fundirse. 
    .row.justify-content-center.mb-5
      .col-lg-6.mb-lg-0.mb-3(data-aos="fade-right")
        .tarjeta-avatar
            img(src='@/assets/curso/temas/2/11.png' alt='AvatarTop' style='width: 300px')
            .tarjeta.w-100(style="background-color: #fdf8e4")
              .p-4
                h5.text-center Elastmeros
                p Se caracterizan por su gran elasticidad y capacidad de estiramiento y rebote, recuperando su forma original una vez que se retira la fuerza que los deformaba.
      .col-lg-6(data-aos="fade-left"): img(src='@/assets/curso/temas/2/12.svg', alt='')
    h4 Identificacin
    p.mb-4(data-aos='fade-right') Por otra parte, se estableci un sistema de clasificacin para facilitar la identificacin de los plsticos, esto se reconoce al mirar la pieza de plstico: se encuentra el smbolo del reciclaje ms un nmero. De esta manera, se facilita mucho el reciclaje del plstico, al identificar rpidamente y sin errores el tipo de polmero de que se trata.
    div(style="background-color: #d6efff").p-5.mb-5(data-aos="zoom-in")
      p.mb-5(data-aos='fade-right') #[b En relacin directa con lo anteriormente dicho, los requisitos que debe cumplir un residuo plstico para ser reciclado son: ]
      .row.justify-content-center.mb-5.align-items-start
        .col-lg-4.col-6.mb-lg-0.mb-3: img(src='@/assets/curso/temas/2/13.png', alt='')
        .col-lg-8
          ul.lista-ul--color
            li.d-flex
              i.fas.fa-check(style='color: #feb12e')
              p.mb-0 No haber estado en contacto con sustancias txicas, como agroqumicos o residuos peligrosos, ni con bacterias patgenas.
            li.d-flex
              i.fas.fa-check(style='color: #feb12e')
              p.mb-0 Sus propiedades mecnicas no deben estar deterioradas.
            li.d-flex
              i.fas.fa-check(style='color: #feb12e')
              p.mb-0 Separar los residuos plsticos de acuerdo con su forma: envases, bolsas y pastas.
            li.d-flex
              i.fas.fa-check(style='color: #feb12e')
              p.mb-0 Separar las etiquetas, tapas y eliminar materiales extraos, como ganchos o residuos de comida.
            li.d-flex
              i.fas.fa-check(style='color: #feb12e')
              p.mb-0 Clasificar los materiales segn sea el tipo de polmero (1 al 7).
            li.d-flex
              i.fas.fa-check(style='color: #feb12e')
              p.mb-0 Lavar el material, pero no se debe utilizar soda custica, detergentes o solventes fuertes; se debe escurrir y secar el material plstico, y controlar la calidad de las resinas recuperadas.
            li.d-flex
              i.fas.fa-check(style='color: #feb12e')
              p.mb-0 Molerlo, segn el tipo de polmero (opcional).
            li.d-flex
              i.fas.fa-check(style='color: #feb12e')
              p.mb-0 Almacenar en sitios cubiertos y con poca humedad.
            li.d-flex
              i.fas.fa-check(style='color: #feb12e')
              p.mb-0 Los pegantes se deben retirar del producto recuperado.
            li.d-flex
              i.fas.fa-check(style='color: #feb12e')
              p.mb-0 Se deben evitar materiales o productos impresos.
            li.d-flex
              i.fas.fa-check(style='color: #feb12e')
              p.mb-0 Preferiblemente, no deben tener grasas u otros materiales viscosos.
    .row.justify-content-center.mb-5(data-aos="zoom-in")
      .col-lg-8
        .tarjeta.p-5(style="background-color: #dee9f0 ")
              .row.justify-content-center.align-items-center.mb-5
                .col-1: img(src='@/assets/curso/temas/atencion.svg', alt='')
                .col-lg-11
                  p.mb-0.fw-bold Para profundizar en este punto del contenido, se le invita a consultar cuidadosamente los siguientes recursos. 
              a.anexo.mb-4(:href="obtenerLink('downloads/Anexo_2_Tabla2_TiposPolimeroPlasticos.pdf')" target="_blank")
                .row.justify-content-around.align-items-center
                  .anexo__icono
                    img(src="@/assets/template/icono-pdf.svg" style="width: 50px")
                  .anexo__texto
                    p #[b Anexo.] Tipos de polmeros de plstico 
              a.anexo.mb-4(href="https://www.youtube.com/watch?v=sSgsT7D5fVY" target="_blank" type="application/pdf")
                .row.justify-content-around.align-items-center
                  .anexo__icono
                    img(src="@/assets/template/icono-link.svg")
                  .anexo__texto
                    p #[b Video.] Reciclaje del plstico y proceso de produccin.
              a.anexo.mb-4(href="https://www.youtube.com/watch?v=eDD9sO8SkOY" target="_blank" type="application/pdf")
                .row.justify-content-around.align-items-center
                  .anexo__icono
                    img(src="@/assets/template/icono-link.svg")
                  .anexo__texto
                    p #[b Video.] El paso a paso en el reciclaje de una botella PET.
    Separador
    #t_2_3.titulo-segundo.color-acento-contenido(data-aos='fade-right')
      h2 2.3 Tetra Pak
    .row.justify-content-center.align-items-center.mb-5
      .col-lg-5.col-8.mb-lg-0.mb-3(data-aos="fade-right"): img(src='@/assets/curso/temas/2/14.png', alt='')
      .col-lg-7(data-aos="fade-left") 
        p El principal objetivo ambiental del Tetra Pak es minimizar los impactos ambientales de sus productos a lo largo de todo su ciclo de vida, haciendo un uso eficiente de recursos en el desarrollo y diseo de los mismos, en sus procesos de manufactura y en sus operaciones.
    .mn(style="background-color: #ecf7f4").p-5.mb-5(data-aos="zoom-in")
      p.mb-5 Conozca algunas generalidades que vale la pena tener en cuenta sobre el Tetra Pak en los procesos de reciclaje y medio ambiente: 
      TabsC.color-acento-contenido.mb-5
        .p-4.p-md-5(titulo="Materias primas renovables" style="background-color: #d3ede5 ")
            .row.align-items-center.justify-content-center
              .col-lg-8.mb-4.mb-md-0
                .tarjeta.bg-white.p-4.rounded-0
                  p El Tetra Pak busca que sus envases provengan en un 100% de materias primas renovables, para lo cual trabaja de la mano con los proveedores de pulpa (donde el objetivo es que todos tengan certificacin FSCTM), de aluminio y plstico. Igualmente, todas las acciones de diseo de nuevos envases y sistemas de llenado buscan que estos sean ms eficientes, reciclables y con mayor porcentaje de materiales; todas las plantas a nivel mundial estn certificadas con la norma ISO 14001, enfocndose en programas de reduccin, minimizacin, mitigacin y compensacin de impactos ambientales en el proceso productivo.
              .col-lg-4.col-8: img(src='@/assets/curso/temas/2/15.png', alt='')
        .p-4.p-md-5(titulo="En cuanto a reciclaje" style="background-color: #d3ede5 ")
            .row.align-items-center.justify-content-center
              .col-lg-8.mb-4.mb-md-0
                .tarjeta.bg-white.p-4.rounded-0
                  p Tetra Pak est invirtiendo en la colocacin de equipos en la industria papelera y de aglomerados, para que estas industrias aprovechen los envases de Tetra Pak posconsumo y posindustria como materia prima. De esta forma, estas empresas fabrican papel, cajas corrugadas, cartn gris, tubos de cartn, muebles, lminas aglomeradas y tejas. En Colombia, existen tres plantas de reciclaje de pulpa de papel, que incluyen el reciclaje de los envases de Tetra Pak, ubicadas en las ciudades de Bogot, Cali y Medelln (planta integrada). Por otro lado, existen dos plantas que aprovechan los residuos de polietileno y aluminio provenientes del despulpado de los envases de Tetra Pak, una en Bogot y una en Medelln.
              .col-lg-4.col-8: img(src='@/assets/curso/temas/2/16.png', alt='')
        .p-4.p-md-5(titulo="Pasos claves" style="background-color: #d3ede5 ")
            .row.align-items-center.justify-content-center
              .col-lg-8.mb-4.mb-md-0
                .tarjeta.bg-white.p-4.rounded-0
                  p Es importante que las fuentes de material de las bodegas, principalmente las de origen domstico, comercial e institucional, que consumen los envases de #[b Tetra Pak], realicen tres sencillos pasos antes de desecharlos en el contenedor de residuos reciclables: enjuagar, escurrir y aplanar. nicamente cuando algn operador o bodega maneje material posindustrial que ha tenido contacto con productos (leches y jugos) en algn cliente industrial (por ejemplo, Colanta, Alpina, Incolacteos, Parmalat, Postobn, etc.), es necesario lavarlo con abundante agua, debido al alto contenido de lquido. No es necesario separar los envases por tipo, esto quiere decir que se pueden mezclar envases de Tetra Pak de leche, jugos y licores sin problema. 
              .col-lg-4.col-8: img(src='@/assets/curso/temas/2/17.png', alt='')
    p.mb-5(data-aos='fade-right') En el siguiente recurso se muestran algunos requerimientos y necesidades del proceso de reciclaje del Tetra Pak. Se recomienda tomar nota atenta de los aspectos ms importantes. Adelante!
    .tarjeta.rounded-pill.p-2.d-inline-block.p-2.color-primario.mb-4(data-aos="fade-right")
          h4.text-white.mb-0.mx-4 Reciclaje del Tetra Pak
    .bg-slyde.p-5.mb-5(data-aos="zoom-in")
      SlyderA(tipo="b")
        .row.align-items-center.justify-content-center
          .col-lg-8.mb-4.mb-md-0
            h4 Mquinas compactadoras
            p Si la bodega cuenta con mquina compactadora, siga las siguientes recomendaciones:
            ul.lista-ul--color
              li.d-flex
                i.fa.fa-check(style="color: #feb12e")
                p.mb-0 Enfardar el material en compactadoras hidrulicas. Si se tiene montacargas, se pueden armar pacas o fardos de mximo 300 kg. Sin embargo, el peso ideal est entre 100 y 150 kg por paca.
              li.d-flex
                i.fas.fa-check(style="color: #feb12e")
                p.mb-0 Si no se tiene montacargas, se deben armar pacas de mximo 50 kg. Armar la paca nicamente con envases posconsumo de Tetra Pak.
              li.d-flex
                i.fas.fa-check(style="color: #feb12e")
                p.mb-0 Verificar que el material est lo ms seco posible, sin contenido de producto o agua. Siempre entregar compactado y nunca envolver las pacas en plstico.
              li.d-flex
                i.fas.fa-check(style="color: #feb12e")
                p.mb-0 No envolver material posindustrial (material proveniente de rollos vencidos) en las pacas. Si hay material posindustrial, es necesario cortarlo a un tamao mximo de 50 cm, para evitar problemas en el proceso de reciclaje.
              li.d-flex
                i.fas.fa-check(style="color: #feb12e")
                p.mb-0 En el proceso de compactacin, colocar cartn a cada lado de la paca para evitar que se desarme fcilmente.
              li.d-flex
                i.fas.fa-check(style="color: #feb12e")
                p.mb-0 Amarrar la paca con alambre de calibre 12 y no con zuncho, porque se puede romper fcilmente.
              li.d-flex
                i.fas.fa-check(style="color: #feb12e")
                p.mb-0 Despus de armada la paca, fumigue con hipoclorito o cualquier inhibidor de malos olores y vectores
          .col-lg-4.col-8
            figure
              img.m-auto(src='@/assets/curso/temas/2/18.png', alt='Texto que describa la imagen')
        .row.align-items-center.justify-content-center
          .col-lg-8.mb-4.mb-md-0
            h4 Almacenamiento de material
            ul.lista-ul--color
              li.d-flex
                i.fas.fa-check(style="color: #feb12e")
                p.mb-0 Cuando ya estn armadas las pacas, se debe designar un lugar dentro de la bodega para almacenarlas.
                  br
                  |En el proceso de almacenamiento, puede seguir las siguientes recomendaciones:
            ul.lista-ul--color.ms-3

              li.d-flex.ms-3
                i.fas.fa-check(style="color: #feb12e")
                p.mb-0 Si no se tiene montacargas, se recomienda apilar en mximo 2 niveles dentro del espacio asignado para almacenar el material.
              li.d-flex.ms-3
                i.fas.fa-check(style="color: #feb12e")
                p.mb-0 Cuando no se cuente con compactadora hidrulica, se puede almacenar el material suelto, nicamente teniendo en cuenta que este debe ir en una lona o big bag (globo) separado de otros materiales.
          .col-lg-4.col-8
            figure
              img.m-auto(src='@/assets/curso/temas/2/19.png', alt='Texto que describa la imagen')
        .row.align-items-center.justify-content-center
          .col-lg-8.mb-4.mb-md-0
            h4 Entrega a planta de reciclaje
            p  Todas las plantas de reciclaje de envases de Tetra Pak reciben el material compactado o suelto. Para la recoleccin, tenga en cuenta:
            ul.lista-ul--color
              li.d-flex
                i.fas.fa-check(style="color: #feb12e")
