    #t_2_4.titulo-segundo.color-acento-contenido(data-aos='fade-right')
      h2 2.4 Vidrio
    .bloque-derecha(data-aos="zoom-in")
      .bloque-texto-g.color-acento-contenido.p-3.p-sm-4.p-md-5
        .bloque-texto-g__img(
          :style="{'background-image':`url(${require('@/assets/curso/temas/2/23.jpg')})`}"
        )
        .bloque-texto-g__texto.p-4
          p.mb-0 En Colombia, el mercado del reciclaje de vidrio inici en 1978, desde entonces la reinsercin de este material a la cadena productiva ha disminuido el ingreso del mismo a rellenos sanitarios en el pas. El vidrio es un material producto de la mezcla de algunos recursos naturales no txicos, como la arena, la caliza, la soda y el feldespato. Estos materiales permiten que su ciclo de vida sea perfecto. En el reciclaje est el comienzo y el final de cada envase de vidrio.
    div(style="background-color: #f3f9ff ").p-5.mb-3(data-aos="zoom-in")
      p Sobre el vidrio en los procesos de reciclaje, ten presente:
      TabsA.color-acento-contenido.mb-5
        .tarjeta.tarjeta-azulc.p-4(titulo="Sostenibilidad")
          p El vidrio es un producto que cumple con el marco de la sostenibilidad ambiental, porque puede ser reciclado varias veces, debido a que su calidad, pureza y transparencia lo permite, minimizando los impactos ambientales negativos en los rellenos sanitarios; adems, genera beneficios sociales, porque se generan ingresos directos e indirectos, y es econmicamente viable, porque los materiales se regresan al ciclo productivo y no se utiliza tanta materia prima.
          img(src='@/assets/curso/temas/2/24.png', alt='')
        .tarjeta.tarjeta-azulc.p-4(titulo="Luego del consumo inicial")
          p Adems de esto, el vidrio posconsumo se convierte en el componente ms importante en la fabricacin de nuevas botellas, lo que permite un ahorro significativo de energa: 1 kg de vidrio reciclado ahorra la energa equivalente a 25 minutos de un computador encendido y cada tonelada de vidrio reciclado evita el consumo de 1.2 toneladas de materia prima virgen y previene la emisin de ms de media tonelada de gases de efecto invernadero. El trmino casco se utiliza a nivel industrial para denominar al vidrio posconsumo.
          img(src='@/assets/curso/temas/2/25.png', alt='')
        .tarjeta.tarjeta-azulc.p-4(titulo="Cadena del vidrio")
          p Esta es la cadena bsica del vidrio
          .titulo-sexto.color-acento-contenido(data-aos='fade-right')
            h5 Figura 2
            span.fst-italic Cadena bsica del vidrio
          img(src='@/assets/curso/temas/2/26.svg', alt='')
          figcaption Nota. ANDI (2017). Calidades de materiales para reciclaje. p.20
        .tarjeta.tarjeta-azulc.p-4(titulo="Requisitos para su reciclaje")
          p La calidad del material que se encuentra discriminado para la compra es: material sucio y material limpio. Para que el proveedor pueda garantizar la calidad del material, es necesario contar con una preparacin previa, la cual se describe a continuacin:
          ol.lista-ol--cuadro
            li
              .lista-ol--cuadro__vineta(style="background-color: #feb12e")
                span(style="color:#243943").fw-bold A
              | Separar: se trata de clasificar por colores blanco, mbar, verde, y, a su vez, clasificarlos por limpio y sucio. Quitar tapas, anillos y dems elementos ajenos al material, catalogados como contaminantes del vidrio.
            li
              .lista-ol--cuadro__vineta(style="background-color: #feb12e")
                span(style="color:#243943").fw-bold B
              | Quebrar: esta es una recomendacin al proveedor, ms no es una condicin para la venta del material, es decir que el vidrio puede entregarse completo; sin embargo, se recomienda al proveedor efectuar la rotura, puesto que disminuye volumen en el transporte.

            li
              .lista-ol--cuadro__vineta(style="background-color: #feb12e")
                span(style="color:#243943").fw-bold C
              | Almacenar: durante el almacenamiento, no mezclar las calidades de limpio y sucio, ni los diferentes tipos de colores. 
    p.mb-5(data-aos='fade-right') #[b Analice la siguiente tabla en la que se presentan los tipos de vidrio:]
    .titulo-sexto.color-acento-contenido(data-aos='fade-right')
      h5 Tabla 1
      br
      span.fst-italic Tipos de vidrio
    .row.justify-content-center.mb-5
      .col-lg-10(data-aos="zoom-in")
