            thead(style="border-top: 0")
              tr
                th(colspan='3' style="background-color: #badc6d") VIDRIO POSCONSUMO COMERCIALIZABLE 
            tbody.text-center
              tr(style="background-color: #e2eacd ").fw-bold
                td TIPO DE VIDRIO POSCONSUMO COMERCIALIZABLE
                td DESCRIPCIN
                td CONTAMINANTES DEL PROCESO
              tr
                td Vidrio envase mbar
                td Se compra completo o roto. No debe mezclarse con otros colores ni con vidrio plano. El vidrio de refractarias o cafeteras no es reciclable, por tanto, no debe mezclarse con ningn tipo de vidrio posconsumo para venta.
                td(rowspan="5") Material ferroso (magntico): puntillas, tapas, clavos, varillas, limadura de hierro, alambre o cualquier elemento magntico. 
                  br
                  br
                  |Material no ferroso (no magntico): principalmente el aluminio, en cualquier presentacin, cobre, zinc, estao, plomo. 
                  br
