                  |Vidrios especiales: espejos, vidrio laminado (automviles), bombillos, fardas, tubos fluorescentes, vidrio de ampolletas. 
                  br
                  br
                  |Material inorgnico: piedras de cualquier tipo, gravilla, arena, cemento, concreto, cermica, vidrio refractario o de refractarias. 
                  br
                  br
                  |Materiales orgnicos: trapos, papel, cartn, madera, plstico (bolsas, pitillos), caucho, tierra, jeringas.
              tr(style="background-color: #f6f6f6") 
                td Vidrio envase verde
