                td Se compra completo o roto. No debe mezclarse con otros colores ni con vidrio plano.
              tr(style="background-color: #f6f6f6") 
                td Vidrio plano transparente
                td Se compra completo o roto. No debe mezclarse con otros colores ni con envases. El espejo no es reciclable, por tanto, no debe mezclarse con ningn tipo de vidrio posconsumo para venta.
              tr 
                td Vidrio plano bronce
                td Se compra completo o roto. No debe mezclarse con otros colores ni con envases. El espejo no es reciclable, por tanto, no debe mezclarse con ningn tipo de vidrio posconsumo para venta.
