        .col-lg-8
          p.mb-0.p-2 El acero y la chatarra ferrosa son infinitamente reutilizables en la naturaleza, aunque se tienen unas mermas en su procesamiento. La produccin de acero a partir del reciclaje de chatarra ferrosa es amigable y sostenible con el medio ambiente, porque disminuye el consumo de recursos naturales no renovables. El acero producido a partir del reciclaje y fundicin de chatarra ferrosa queda en condicin de acero nuevo, cumpliendo con todas las caractersticas fsico-mecnicas exigidas en las normas nacionales e internacionales para la fabricacin de acero.
    .pt-lg-5.pt-0   
      p #[b La mayora de siderrgicas colombianas son semiintegradas, lo que implica que su materia prima es la chatarra ferrosa.] La chatarra ferrosa se puede procesar, manipular, preparar y fundir, siempre y cuando no presente un riesgo para la salud de las personas, el medio ambiente o riesgo para los bienes e instalaciones.
