        )
        .bloque-texto-g__texto.p-4
          p.mb-0 #[b Los RAEE son residuos de manejo diferenciado y se prohbe su disposicin final en rellenos sanitarios.] Deben ser recolectados y gestionados por los productores y se prioriza su aprovechamiento mediante empresas gestoras que cuenten con licencia ambiental para ello. Es decir, la normativa busca incentivar el aprovechamiento de los residuos de aparatos elctricos y electrnicos desde una perspectiva econmica y social, y promover la participacin e integracin de todos los actores involucrados en la elaboracin de las estrategias, planes y proyectos que desarrollen la gestin integral de los RAEE, entre otros aspectos.
