        .col-lg-9 
          p Las dems categoras corresponden a los otros tipos de aparatos elctricos y electrnicos que pueden ser usados tanto en el hogar como en las empresas, el comercio o en las instituciones, como los aparatos de alumbrado, las herramientas elctricas y electrnicas (con excepcin de las herramientas industriales fijas de gran envergadura), los juguetes o equipos deportivos y de ocio, los aparatos mdicos (con excepcin de todos los productos implantados e infectados), los instrumentos de vigilancia y control y las mquinas expendedoras de productos y dinero.
    .bg-2.p-5.mb-5(data-aos="zoom-in")
      .tarjeta.rounded-pill.p-3.d-inline-block.p-2.color-primario.mb-4
          h4.text-white.mb-0.mx-4 Sustancias peligrosas
