        .col-lg-9 
          p Asimismo, se han identificado las sustancias peligrosas que ms comnmente estn presentes en los RAEE. Hay que tener en cuenta que los RAEE pueden o no ser peligrosos; si el RAEE es no peligroso, se debe considerar si es especial o reciclable o no aprovechable; y si es peligroso, la forma de saberlo es teniendo en cuenta su composicin.
    .bg-1.p-5.mb-5(data-aos="zoom-in")
      .tarjeta.rounded-pill.p-3.d-inline-block.p-2.color-primario.mb-4
          h4.text-white.mb-0.mx-4 Segn la ley
      .row.justify-content-center.mb-5
        .col-lg-3.col-6.mb-lg-0.mb-3: img(src='@/assets/curso/temas/2/39.png', alt='')
        .col-lg-9 
          p La Ley 1672 de 2013 le da el carcter de manejo diferenciado a los RAEE, prohbe su disposicin final en los rellenos sanitarios y los restringe a rellenos de seguridad, para que sean retomados por los productores de aparatos elctricos y electrnicos, mediante sistemas de recoleccin y de gestin ambientalmente segura. As, se busca que el consumidor devuelva los RAEE sin ningn costo.
